import React from 'react';
import axios from 'axios';
import 'yokui/css/positioning';
import { ErrorAlert } from 'yokui/react/alerts';
import { Form } from 'yokui/react/forms';
import { Input } from 'yokui/react/inputs';
import { PrimaryButton } from 'yokui/react/buttons';
import { FlexCol, Grid } from 'yokui/react/flex-grids';

export default class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state.reqResult = false;
  }

  render() {
    return (
      <Form {...{
        onSubmit: ({initial, current}) => {
          axios.post(process.env.API_URL + '/users', current)
            .then((result) => {
              console.log(result);
            })
            .catch((error) => {
              console.log(error);
              this.setState({reqResult: true});
            });
        },
        fields: {
          name: {
            label: 'Nama Lengkap',
            children: <Input type='text' placeholder='Sesuai identitas diri'/>
          },
          email: {
            label: 'Email',
            children: <Input type='email' placeholder='Email untuk verifikasi' />
          },
          password: {
            label: 'Password',
            children: <Input type='password' placeholder='Minimal 6 karakter' />
          }
        }
      }}>
        {({fields}) => {
          const { reqResult } = this.state;

          return (
            <div>
              <Grid>
                <FlexCol />
                <FlexCol>
                  <ErrorAlert withIcon show={reqResult}>File 'abc.txt' failed to upload.</ErrorAlert>

                  {fields.name}
                  {fields.email}
                  {fields.password}

                  <div class='float-right'>
                    <PrimaryButton {...{
                      type: 'submit',
                    }}>Daftar</PrimaryButton>
                  </div>
                </FlexCol>
                <FlexCol />
              </Grid>
            </div>
          )
        }}
      </Form>
    )
  }
}
